.event.list .subnav-menu {
    .title {
        border-right: 0; }
    .title h2 {
        font-size: 1.15em;
        margin-bottom: .75em; }
    ul.context {
        list-style: none;
        padding-inline-start: 0;
        border: var(--border-line);
        border-radius: 10px;
        overflow: hidden; }
    .context li {
        border-bottom: var(--border-line);
        padding: 1em 1.33em;
        font-size: .86em; }
    .context li:nth-last-child(1) {
        border-bottom: 0; } }
