.event.list {
    a:not(.button) {
        color: var(--text); }
    ul {
        list-style: none;
        padding-left: 0; }
    .heading {
        border-bottom: var(--border-line);
        margin-top: 1em;
        margin-bottom: 2em;
        padding-bottom: .25rem;
        display: flex;
        align-items: baseline;
        .headsub {
            font-size: 1.5em; }
        h6 {
            font-weight: 500;
            margin-left: auto; } }
    .row,
    .main,
    .taxo,
    .years,
    .month,
    .yearlist,
    .monthlist {
        display: grid;
        grid-gap: 2rem;
        .row {
            grid-template-columns: 3rem auto;
            margin-bottom: 2em;
            padding-bottom: 2em;
            border-bottom: var(--border-line);
            .box {
                border-right: 1px solid orange;
                width: 3rem;
                height: 3rem;
                .date {
                    font-weight: 600;
                    font-size: 1.5rem; }
                .month {
                    font-size: .9rem; } } } }
    .main {
        grid-template-columns: 21% auto 21%;
        margin-top: 3em; }
    .years,
    .month, {
        grid-template-columns: auto 23%;
        margin-top: 3em;
        grid-gap: 3rem;
        .listevents {
            .row {
                border-bottom: 0;
                margin: 0;
                padding: 1em 0; }
            .box .month {
                margin-top: 0; } } }
    .yearlist {
        grid-template-columns: 20% auto;
        border-bottom: var(--border-line);
        padding: 2rem 0;
        .heading {
            display: block;
            border-bottom: 0; } }
    .monthlist {
        grid-template-columns: auto auto;
        .thumb,
        .thumb img,
        .slider-con {
            height: 17rem; } }
    .listevents {
        margin-bottom: 1.4em;
        .title {
            font-size: 1.05rem; }
        .leads {
            font-size: .85re; } } }
