.upcoming {
    a:not(.button) {
        color: var(--text); }
    .row {
        display: grid;
        grid-template-columns: auto 60%;
        align-items: center;
        grid-gap: 2em;
        @media screen and (max-width: 980px) {
            grid-auto-flow: dense;
            grid-template-columns: auto; } }
    .thumb {
        @media screen and (max-width: 980px) {
            grid-row: 1; } }
    .slider-con,
    .slider img {
        @media screen and (max-width: 1360px) {
            height: 26rem; } }
    .headsub {
        font-size: 1.5rem;
        font-weight: 500; }
    .bordline {
        background: orange;
        margin-top: .25em;
        padding-top: 0;
        width: 45%; }
    .dates {
        font-weight: 600;
        font-size: 1.1rem;
        color: var(--leads); }
    .title {
        font-weight: 600;
        font-size: 2rem; }
    .leads {
        font-size: .93em; } }
